class View {
    createForm(){
        let form = document.getElementById('form');
        let createForm = '<form>' +
                '<input type="text" name="login" placeholder="login" maxlength="25" size="40" autocomplete="of"/>' +
                '<input type="password" name="password" placeholder="password" maxlength="25" size="40" autocomplete="of"/>' +
                '<button class="form-button">submit</button>' +
            '</form>';

        form.appendChild(createForm);
    }
}

export default View



